<template>
	<view class="duihuan">
		<view class="duihuan-wrap">
			<view class="search_wrap">
				<view class="search_item" v-for="(item,index) in duihuanList" :key="index">
					<view class="search_item_left">
						<view class="search_left_title">{{item.F_FullName}}</view>
						<view class="search_left_text">
							<text class="iconfont icon-dizhi"></text>
							{{item.F_Address}}
						</view>
					</view>
					<view class="search_item_right" @click="handleMap(item)">
						<view class="search_right_num">{{ item.distance | numFilter }}km</view>
						<view class="item-right">
							<image src="../../static/shang.png" mode="widthFix"></image>
						</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>
<script>
	import common from '../../common/common'
	var _self;
	export default {
		data() {
			return {
				duihuanList: []
			}
		},
		onLoad() {
			_self = this;
			_self.handleList();
		},
		filters: {
		  numFilter (value) {
		    let realVal = ''
		    if (!isNaN(value) && value!== '') {
		      // 截取当前数据到小数点后两位
		      realVal = parseFloat(value).toFixed(2)
		    } else {
		      realVal = '--'
		    }
		    return realVal
		  }
		},
		methods: {
			handleList() {
				const { latitude,longitude } = uni.getStorageSync("jingwei");
				common.request({
					path: "Order/getoutlets",
					data: {
						name: '',
						longitude,
						latitude,
						rows: 10,
						page: 1,
						sidx: "",
						sord: ""
					},
					success: res => {
						_self.duihuanList = res.data.rows					
					}
				})
			},
			handleMap(item){
				let F_latitude = parseFloat(item.F_latitude)
				let F_longitude = parseFloat(item.F_longitude)				
				if(F_latitude && F_longitude){					
					uni.openLocation({
						latitude: F_latitude,
						longitude: F_longitude,
						name: item.F_FullName,
						address:item.F_Address,
						success(res) {
						},
						fail(err) {
						}
					})
				}else{
					uni.showToast({
						title: '获取商家地址失败',
						icon: 'none',
						image: '',
						duration: 1500,
						mask: true,
					})
				}
			}
		}
	}
</script>
<style lang="scss" scoped>
	.duihuan {
		.search_wrap {
			.search_item {
				display: flex;
				justify-content: space-between;
				width: 88%;
				margin: 20rpx auto 0;
				padding: 20rpx;
				align-items: center;
				background-color: #fff;
				border-radius: 8rpx;
				.search_item_left {					
					.search_left_text {
						color: #999;
						margin-top: 20rpx;
						text{
							margin-right: 10rpx;
						}						
					}
				}
				.search_item_right {
					display: flex;
					flex-direction: row;
					align-items: center;
					.search_right_num {
						color: #999;
						margin-right: 10rpx;
					}
					.item-right {						
						image{
							width: 20rpx;
							height: 20rpx;
							transform: rotate(90deg); 
						}
					}	
				}
			}
		}
	}
</style>
